* {
  //  border: 1px solid red;
  margin: 0px;
}

.phone {
  display: none;
}

body {
  margin: 0;
  font-family: "Alatsi", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("assets/bg.png");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  height: 100vh;
  align-items: center;
  display: flex;
  justify-content: center;
  color: white;
}

.details_container {
  background-color: #6b6b6b;
  border-radius: 70px;
  height: 80vh;
  width: 90vw;
  display: flex;
  /* justify-content: space-between; */
  /* padding-left: 50px; */

  .informations {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 80vh;

    .img_profil_details {
      height: 35%;
      width: auto;
      border-radius: 10000px;
      border: 1.4vh solid #262626;
      padding-top: 0px;
    }

    .nume {
      /* font-size: 32px; */
      font-size: 5vh;
      font-weight: 100;
      padding: 0px;
    }

    .job {
      font-size: 4vh;
      font-weight: 100;
      padding-top: 0px;
    }

    .socials {
      display: flex;
      justify-content: space-between;
      margin-top: 30px;
    }

    .socials > * {
      border: 0.4vh solid white;
      border-radius: 100px;
      height: 4.7vh !important;
      width: 4.7vh !important;
      margin: 0.8vh;
    }

    .socials > *:hover{
      background-color: #262626;
      transform: scale(110%);
    }

    .cv_download {
      background-color: transparent;
      border: 0.4vh solid white;
      border-radius: 100px;
      margin: 0;
      padding: 10px 15px;
      text-align: inherit;
      font: inherit;
      font-size: 2.4vh;
      color: white;
      margin-top: 10px;
      text-decoration: none;
      cursor: pointer;
    }

    .cv_download:hover{
      background-color: #262626;
      transform: scale(110%);
    }
  }

  .informations > * {
    padding-top: 10px;
  }
}

@media only screen and (max-width: 800px) {
  .phone {
    display: block;
  }
  .informations {
    display: none !important;
  }
  .details_container {
    width: 100vw;
    height: 100vh;
    border-radius: 0px;
  }
  .navbar {
    position: fixed;
    width: 100vw;
    display: flex;
    z-index: 3;
    // padding: 2vh;
    flex-direction: row;
    justify-content: space-between;
    .flothing-name {
      // position: fixed;
      width: fit-content;
      font-size: 3vh;
      margin-left: 2vh;
      margin-top: 3vh;
      padding: 1vh;
      padding-left: 1.5vh;
      padding-right: 1.5vh;
      opacity: 80%;
      border-radius: 30px;
      background-color: #262626;
    }
    .hamburger-button {
      background-color: white;
      width: 48px;
      height: 48px;
      margin-right: 2vh;
      margin-top: 2vh;
      border-radius: 100%;
      z-index: 4;

      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      cursor: pointer;

      .bar1,
      .bar2,
      .bar3 {
        width: 28px;
        border-radius: 10px;
        height: 5px;
        background-color: #333;
        margin: 2px 0;
        transition: 0.4s;
        
      }

      .change.bar1 {
        transform: translate(0, 9px) rotate(-45deg);
      }

      .change.bar2 {
        opacity: 0;
      }

      .change.bar3 {
        transform: translate(0, -9px) rotate(45deg);
      }
    }
  }
}
