.BurgerMenu{
    position: fixed;
    height: 100%;
    width: 80vw;
    right: -80vw;
    top: 0px;
    z-index: 1;
    background-color: #6b6b6b;
    
    transition: 0.7s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   

    .img_profil_details {
      height: 25%;
      width: auto;
      border-radius: 10000px;
      border: 1.4vh solid #262626;
      padding-top: 0px;
    }

    .nume {
      /* font-size: 32px; */
      font-size: 5vh;
      font-weight: 100;
      padding: 0px;
    }

    .job {
      font-size: 4vh;
      font-weight: 100;
      padding-top: 0px;
    }

    .socials {
      display: flex;
      justify-content: space-between;
      margin-top: 30px;
    }

    .socials > * {
      border: 0.4vh solid white;
      border-radius: 100px;
      height: 4.7vh !important;
      width: 4.7vh !important;
      margin: 0.8vh;
    }

    .cv_download {
      background-color: transparent;
      border: 0.4vh solid white;
      border-radius: 100px;
      margin: 0;
      padding: 10px 15px;
      text-align: inherit;
      font: inherit;
      font-size: 2.4vh;
      color: white;
      margin-top: 10px;
      text-decoration: none;
    }

}
.show{
    right: 0px;
    transition: 0.7s;
}