.cards::-webkit-scrollbar {
  display: none;
}

.cards {
  background-color: #262626;
  width: 80%;
  border-radius: 70px;
  overflow-y: scroll;
  scrollbar-color: #6969dd #e0e0e0;
  scrollbar-width: thin;
  scroll-snap-type: y mandatory !important;
  // scroll-snap-align: start none !important;
  // z-index: -1;
  // border: 1px solid orange;

  .animateCard {
    opacity: 1 !important;
    transition: all 1s !important;
    transform: translateX(0%)!important;
    filter: blur(0px) !important;
  }

  // .card > * {
  //   opacity: 0;
  //   transition: all 1s;
  //   transform: translateX(-5%);
  //   filter: blur(5px);
  // }

  .card > * {
    opacity: 0.1;
    transition: all 1s;
    transform: translateX(-5%);
    filter: blur(5px);
    // border: 1px solid orange;
  }

  // .continut > *:nth-child(2) {
  //   transition-delay: 2s !important;
  //   // display: none !important;
  // }
  // .continut > *:nth-child(3) {
  //   transition-delay: 400ms;
  // }

  .card {
    display: flex;
    // border: 1px solid red;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 81vh;
    scroll-snap-align:start !important;
    scroll-margin: 1px !important;
    scroll-padding-top: 1px;

    .titlu-card {
      font-size: 6vh;
      height: 15%;
      text-align: center;
      padding-top: 2vh;
    }

    .continut {
      height: 80%;
      width: 100%;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
    }

    i {
      font-size: 20px;
      height: 7%;
    }
  }
}

.card-prezentare {
  .continut {
    flex-direction: row !important;
    padding-top: 5vh;
  }

  .img_profil_card {
    height: 40vh;
    width: 40vh;
    border-radius: 1000px;
    border: 1.5vh solid #6b6b6b;
    padding-top: 0px;
    margin-right: 4vw;
  }

  .text {
    max-width: 35%;
    text-align: left;

    h1 {
      font-size: 5vh;
    }

    .prezentare {
      font-size: 3vh;
    }
  }
}

.card-coding {
  .grafice {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    //padding-bottom: 80px;
  }
  .grafice > * {
    margin: 2.5vh;
  }
}

.card-education {
  .continut {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    //padding-bottom: 80px;
  }
}

.card-experiences {
  .continut {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    //padding-bottom: 80px;
  }
}

.card-social {
  .continut {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
}

@media only screen and (max-width: 800px) {
  .cards {
    width: 100%;
    height: 100%;
    border-radius: 0px;
    .card {
      // height: 100%;
      min-height: 100vh;
      height: fit-content;
      justify-content: start;
      padding-top: 5vh;
      // padding-bottom: 10vh;
      // margin-top: 6vh;
      // margin-bottom: 6vh;
      // border: 1px solid red;

      .titlu-card {
        margin-top: 3vh;
        margin-bottom: 5vh;
      }

      // .sageata_next {
      //   display: none;
      // }

      .DotList {
        width: 90vw;
      }
    }

    .card-prezentare {
      padding-top: 5vh;
      .continut {
        .img_profil_card {
          width: 40vw;
          height: 40vw;
        }
        .text {
          max-width: 80%;
          margin-top: 5vh;
          h1 {
            text-align: center;
            margin-bottom: 1vh;
            font-size: 4vh;
          }
          h2 {
            text-align: center;
            margin-bottom: 1vh;
            font-size: 3vh;
          }
          p {
            text-align:start;
            font-size: 2ch;
            
          }
        }
      }
    }

    .card-coding {
      .grafice > * {
        width: 70vw;
        margin: 1vh;
      }
    }

    .card-education {
      .TimeList {
        width: 90vw;
      }
    }

    // .card-experiences{
    //   // padding-bottom: 10vh;

    // }
  }
}

@media only screen and (max-width: 400px) {
  .cards {
    .card-prezentare {
      .continut {
        .img_profil_card {
          width: 60vw;
          height: 60vw;
        }
      }
    }
  }
}
