.DotList{
    display: flex;
    width: 80%;

    .st{
        display: flex;
        width: 10%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .vl{
            border-left: 1px solid white;
            height: 90%;
            margin-top: 5px;
            margin-bottom: 5px;
        }
        .dot {
            margin: 0px;
            height: 2vh;
            width: 2vh;
            background-color: white;
            border-radius: 100%;
            display: inline-block;
          }
    }

    .dr{
        padding-bottom: 10px;
        text-align: left;
        // padding-left: 20px;
        font-weight: 100;
        width: 90%;
        h2{
            font-size: 3vh;
            font-weight: 100;
        }
        h3{
            font-size: 2.1vh;
            font-weight: 100;
        }
    }

    
}

@media only screen and (max-width: 800px) {
    .DotList{
        .dr{
            h2{
                font-size: 2.6vh;
                font-weight: 100;
            }
            h3{
                font-size: 1.9vh;
                font-weight: 100;
            }
        }
    
        
    }

}